import React from "react";
const data = [
  {
    id: "1-game",
    label: "Fun Game",
    isFunStuff: undefined,
    isGreat: false,
    childData: [
      {
        id: "1-game-child",
        label: "Child Fun game 1",
        isFunStuff: true,
        isGreat: false
      },
      {
        id: "2-game-child",
        label: "Child Fun game 2",
        isFunStuff: true,
        isGreat: false
      }
    ]
  },
  {
    id: "1-game",
    label: "Fun Game",
    isFunStuff: true,
    isGreat: undefined,
    childData: undefined
  },
  {
    id: "1-game-child",
    label: "Child Fun game 1",
    isFunStuff: true,
    isGreat: false
  },
  {
    id: "2-game-child",
    label: "Child Fun game 2",
    isFunStuff: undefined,
    isGreat: false
  }
];

const removeDuplicateId = (data) => {
  let newObj = {};
  data.forEach((item) => {
    let temp = {};
    const { id } = item;
    Object.keys(item).forEach((key) => {
      // if (newObj[id] && newObj[id][key] !== undefined) {
      //   temp[key] = newObj[id][key];
      // } else {
      //   temp[key] = item[key];
      // }
      temp[key] = item[key] ?? (newObj[id] && newObj[id][key]);
    });
    newObj[id] = temp;
  });
  console.count("removeDuplicateId");

  const transformed = Object.keys(newObj).map((key) => newObj[key]);
  return transformed;
};

export default function App() {
  const [state] = React.useState(() => removeDuplicateId(data));
  const [input, setInput] = React.useState("");
  const [isSearch, setIsSearch] = React.useState(false);

  const handleOnClick = (label) => {
    console.log("clicked", label);
    isSearch && setIsSearch(false);
    setInput(label);
  };

  const handleOnChange = (e) => {
    console.log("called");
    !isSearch && setIsSearch(true);
    setInput(e.target.value);
  };

  const handleOnKeyPress = (e, label) => {
    const { key } = e;
    console.log("key press", key);
    switch (key) {
      case "ArrowUp": {
        console.log("ArrowUp");
        break;
      }
      case "ArrowDown": {
        console.log("ArrowDown");
        break;
      }
      case "Enter": {
        console.log("Enter");
        setIsSearch(false);
        setInput(label);
        break;
      }
      default:
        break;
    }
  };

  const showItemLabels = (data) => {
    return data.length > 0
      ? data.map((item) => (
          <li
            key={item.id}
            tabIndex="0"
            style={{
              listStyle: "none",
              border: "1px solid grey",
              padding: "5px"
            }}
            onClick={() => handleOnClick(item.label)}
            onKeyDown={(e) => handleOnKeyPress(e, item.label)}
          >
            {item.label}
          </li>
        ))
      : null;
  };

  const filterByLabel =
    !!input && !isSearch
      ? state
      : state.filter((item) =>
          item.label.toLowerCase().includes(input.trim().toLowerCase())
        );

  console.count("App");
  return (
    <div style={{ display: "flex" }}>
      <label htmlFor="label">Search By Label: </label>
      <div>
        <input
          id="label"
          name={`label-${Math.random()}`} // adding to remove chrome's suggestions for input
          value={input}
          onChange={handleOnChange}
          placeholder="Search for label"
          style={{ padding: "5px" }}
        />
        {filterByLabel && isSearch && showItemLabels(filterByLabel)}
      </div>
    </div>
  );
}
